<template>
  <div class="app-container">
    <van-nav-bar
      class="bar"
      fixed
      placeholder
      title="答题"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <div class="answer-box">
      <div class="title">{{data.name}}</div>
      <div class="explain">{{data.instruction}}</div>
      <van-progress v-if="percentage" :percentage="percentage" :format="formatPercentage"></van-progress>
      <div class="explain">{{problemData.instruction}}</div>
      <div class="question" v-if="problemData.question">题目:{{problemData.question.question}}</div>
      <div>
        <van-radio-group
          v-if="problemData.question"
          v-model="problemData.question.answer"
         >
          <div class="question"  @click="handleChange" v-for="(item,index) in problemData.question.answers" :key="index">
            <van-radio :name="index">{{item}}</van-radio>
          </div>
        </van-radio-group>
      </div>
    </div>
    <div class="btn-box">
      <van-button type="info" plain @click="prevQuestion">上一题</van-button>
      <van-button
        type="info"
        plain
        @click="nextQuestion"
        v-if="questionLength!=questionNum+1"
      >下一题</van-button>
      <van-button type="info" plain @click="submitData" v-else>提交</van-button>
    </div>
    <div class="btn-box">
      <van-button type="info" plain @click="finishCommit" >完成并提交</van-button>
    </div>
     <van-overlay :show="loading" @click="show = false" >
      <div class="wrapper">
        <van-loading  class="block" size="50px" vertical type="spinner"> 加载中...</van-loading>
      </div>
    </van-overlay>
    <van-dialog v-model="dialogTips" title="提示" show-cancel-button @confirm="appendQuestionSubmitData">
      <div class="tips-box" v-if="tipsData.question">
        <div class="title">问题:{{tipsData.question.question}}</div>
        <van-radio-group v-model="tipsData.question.answer">
            <van-radio class="radio" v-for="(itemData,indexData) in tipsData.question.answers" :name="indexData" :key="indexData">{{itemData}}</van-radio>
        </van-radio-group>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import {
  submitQuestion,
  screeningQuestionSubmit,
  getQuestionnaireDetail,
  appendScreeningQuestion
} from "@/api/question";
import {mapGetters} from 'vuex'
 import {getRecordPatient} from "@/api/patient";
   import {updateMedicalRecord} from "@/api/medicalRecord"
export default {
  name: "question",
  data() {
    return {
      loading: false,
      tipsData:{},
      dialogTips:false,
      completeQuestionnaire:false,
      data: [],
      medicalRecordId:"",
      patientId:"",
      problemData: [],
      questionNum: 0,
      questionLength: 0
    };
  },
  watch: {
    scaleId(newName, oldName) {
      this.handleChangeJSON();
    }
  },
  mounted() {
    console.log(this.info)
   
    if(this.info.examinationStatus==0||this.info.examinationStatus==10){
     
      this.medicalRecordId=this.info.id;
      this.patientId=this.info.patientId;
      this.handleChangeJSON();
    }else{
        this.$router.push("/history")
    }
    
  },
  computed: {
     ...mapGetters([             //步骤二，对象扩展运算符方式
        "info"
      ]),
    percentage() {
      let percent = Math.ceil(
        ((this.questionNum + 1) / this.questionLength) * 100
      );
      if (percent > 100) {
        percent = 100;
      }
      return percent;
    }
  },
  methods: {
    onClickLeft(){
       this.$router.go(-1);
    },
    finishCommit() {
        if(!this.completeQuestionnaire){
            this.$toast("筛查问卷还没有做完")
            return
        } 
        let strHRV=this.hrvComplete?"是否提交":"HRV未完成是否提交？"
          let param={
            id:this.medicalRecordId,
            examinationStatus:100,
          }
          this.loading = true;
          updateMedicalRecord(param).then(res=>{
             this.loading = false;
            if(res.code==200){
               this.$router.push({path:"/home",query:{id:this.medicalRecordId}})
            }

          })
      },
    handleChangeJSON() {
      this.loading = true;
      getQuestionnaireDetail({ medicalRecordId: this.info.id, questionnaireId: this.info.questionnaireNo }).then(
        res => {
          this.loading = false;
          if (res.code == 200) {
            let data = res.dataList[0];
            for (let item of data.questionList) {
              item.question = JSON.parse(item.question);
            }
            this.data = data;
            this.problemData = this.data.questionList[this.questionNum];
            this.questionLength = this.data.questionList.length;
          }
        }
      );
    },
     getPatientData(){
        this.loading = true;
        getRecordPatient(this.medicalRecordId).then(res=>{
           this.loading = false;
          if(res.code==200){
            this.completeQuestionnaire=res.dataList[0].completeQuestionnaire;
            //this.scaleId=res.dataList[0].questionnaireNo
          }
          
        })
      },
    handleChange(e) {
      //this.problemData.question.answer=e.name;
     // if(this.problemData.question.answer !== ""){
          setTimeout(() => {
          this.nextQuestion();
        }, 500);
     // }
    },
    checkAddShow(arr) {
      if (this.problemData.hidden) {
        this.questionNum++;
        this.problemData = arr.problem[this.questionNum];
        this.checkAddShow(arr);
      }
    },
    checkReduceShow(arr) {
      if (this.problemData.hidden) {
        this.questionNum--;
        this.problemData = arr.problem[this.questionNum];
        this.checkReduceShow(arr);
      }
    },
    formatPercentage() {
      return (
        (((this.questionNum + 1) / this.questionLength) * 100).toFixed() + "%"
      );
    },
    prevQuestion() {
      if (this.questionNum <= 0) {
        this.$toast("当前是第一题");
      } else {
        if (this.problemData.preQuestionNum != 0) {
          for (let x = 0; x < this.data.questionList.length; x++) {
            if (
              this.problemData.preQuestionNum ==
              this.data.questionList[x].preQuestionNum
            ) {
              this.questionNum = x;
              break;
            }
          }
          this.questionNum = this.problemData.preQuestionNum;
          this.problemData = this.data.questionList[this.questionNum];
        } else {
          this.questionNum--;
          this.problemData = this.data.questionList[this.questionNum];
        }
      }
    },
    nextQuestion() {
      if (this.questionNum < this.questionLength - 1) {
        if (this.problemData.question.answer === "") {
          this.$toast("请选择答案");
        } else {
          if (
            this.problemData.nextQuestionNum != 0 &&
            this.problemData.question.answer == 0
          ) {
            for (
              let x = this.questionNum;
              x < this.data.questionList.length;
              x++
            ) {
              this.data.questionList[x].question.answer = 0;

              if (
                this.problemData.nextQuestionNum ==
                this.data.questionList[x].questionNum
              ) {
                this.questionNum = x;
                break;
              }
            }
            this.problemData = this.data.questionList[this.questionNum];
          } else {
            this.questionNum++;
            this.problemData = this.data.questionList[this.questionNum];
          }
        }
      } else {
        this.$toast("最后一题了");
      }
    },
     appendQuestionSubmitData(){
          let param={
            questionAnswer:{
              answer:JSON.stringify(this.tipsData.question),
              medicalRecordId:this.medicalRecordId,
              questionNum:this.tipsData.questionNum,
              questionnaireId: this.info.questionnaireNo,
              userId:this.patientId,
            },
            scaleQuestionResult:{
              displayOrder:this.tipsData.displayOrder,
              optionOrderList:[this.tipsData.question.answer],
              optionValue:[this.tipsData.question.answers[this.tipsData.question.answer]],
              questionNum:this.tipsData.questionNum,
              returnValue:[""],
            }
          }
           this.loading = true;
          appendScreeningQuestion(param).then(res=>{
             this.loading = false;
            if(res.code==200){
               this.dialogTips=false;
              this.$toast("提交成功")
              this.getPatientData();
            }
          })
      },
    submitData() {
      if (this.problemData.question.answer === "") {
        this.$toast("请选择答案!");
        return;
      }
      let param = {
        questionnaireId: this.data.id,
        questionnaireNumber: this.data.number,
        medicalRecordId: this.medicalRecordId,
        patientId: this.patientId,
        questionResultList: [],
        questionnaireAnswer: {
          condition: this.data.condition,
          instruction: this.data.instruction,
          medicalRecordId: this.medicalRecordId,
          name: this.data.name,
          number: this.data.number,
          questionnaireId: this.data.id,
          type: this.data.type,
          userId: this.patientId,
          questions: []
        }
      };
      for (let item of this.data.questionList) {
        let qr = {};
        let qas = {};
        if (item.type == "0") {
          qr.displayOrder = item.displayOrder;
          qr.optionOrderList = [item.question.answer];
          qr.optionValue = [item.question.answers[item.question.answer]];
          qr.questionNum = item.questionNum;
          (qr.remark = ""), (qr.returnValue = [""]);
          qas.answer = JSON.stringify(item.question);
          qas.medicalRecordId = this.medicalRecordId;
          qas.questionNum = item.questionNum;
          qas.questionnaireId = this.data.id;
          qas.userId = this.patientId;
        }
        param.questionResultList.push(qr);
        param.questionnaireAnswer.questions.push(qas);
      }
      this.loading = true;
      screeningQuestionSubmit(param)
        .then(res => {
          this.loading = false;
          if (res.code == 200) {
            if(res.dataList.length!=0){
               let tipsData=res.dataList[0];
               tipsData.question=JSON.parse(tipsData.question);
               this.tipsData=tipsData;
               this.dialogTips=true;
                this.$toast(res.message);
            }else{
              this.getPatientData();
            }
             

          } else {
            
            this.$toast(res.message);
          }
        })
        .catch(error => {
          this.loading = false;
        });
    }
  }
};
</script>

<style scoped lang="less">
.flex-column {
  height: calc(100vh - 90px);
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  justify-content: space-between;
}
.app-container {
  height: 100vh;
  background: #ffffff;
  /deep/.van-nav-bar {
    background-color: #1092d4;
    color: #fff;
    .van-icon {
      color: #fff;
    }
    .van-nav-bar__title {
      color: #fff;
    }
  }
  /deep/ .van-nav-bar__text {
    color: #fff;
  }
}
.res-box {
  margin: 10px;
  background: #fff;
  &.img {
    height: calc(100vh - 110px);
  }
  &.scale {
    height: calc(100vh - 110px);
    overflow: auto;
  }
}

.answer-box {
   margin: 10px 30px;
  .explain {
    font-size: 16px;
    color: #333;
    padding: 5px 0;
    line-height: 35px;
    font-weight: bold;
    text-indent: 2em;
  }
  span {
    margin: 0 20px;
  }
  .title {
    width: 100%;
    text-align: center;
    color: #000;
    font-size: 20px;
    font-weight: bold;
  }
}

.question {
  line-height: 35px;
  margin: 10px 0;
}
.question label {
  line-height: 35px;
  max-width: 100%;
  text-overflow: ellipsis;
  white-space: normal;
}
.remark {
  width: 60%;
}
.btn-box {
  width: 100%;
  text-align: center;
  margin: 20px 0;
  /deep/.van-button{
    border-radius: 10px;
  }
}
.symptom {
  line-height: 45px;
}
.van-checkbox {
  margin-right: 5px;
}
.tips-box{
  padding:10px;
  .radio{
   margin:10px 0;
  }
}
</style>



